import React, { useState, useEffect } from 'react';
import Grid from '@mui/material/Unstable_Grid2';
import Divider from '@mui/material/Divider';
import Stack from '@mui/material/Stack';
import Prayer from './Prayer';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import axios from 'axios';
import moment from 'moment';

import fajrImage from './photos/fajr.png'; 
import dhurImage from './photos/dhur.png'; 
import asrImage from './photos/asr.png';
import maghribImage from './photos/maghrib.png'; 
import ishaaImage from './photos/ishaa.png'; 

export default function MainContent() {
  const [city, setCity] = useState({ displayName: 'مكة المكرمة', apiName: 'Makkah al Mukarramah' });
  const avilableCities = [
    { displayName: 'مكة المكرمة', apiName: 'Makkah al Mukarramah', country: 'SA' },
    { displayName: 'الرياض', apiName: 'Riyadh', country: 'SA' },
    { displayName: 'المدينة المنورة', apiName: 'Al Madinah', country: 'SA' },
    { displayName: 'تونس', apiName: 'Tunisia', country: 'TU' },
    { displayName: 'بودابست', apiName: 'Budapest', country: 'HU' }
  ];

  const prayersArray = [
    { key: 'Fajr', displayName: 'الفجر' },
    { key: 'Dhuhr', displayName: 'الظهر' },
    { key: 'Asr', displayName: 'العصر' },
    { key: 'Maghrib', displayName: 'المغرب' },
    { key: 'Isha', displayName: 'العشاء' }
  ];

  const [nextPrayerIndex, setNextPrayerIndex] = useState(0);
  const [today, setToday] = useState('');
  const [remainingTime, setRemainingTime] = useState('');
  const [timings, setTimings] = useState({ Fajr: '', Dhuhr: '', Asr: '', Maghrib: '', Isha: '' });

  useEffect(() => {
    getTimings();
    const t = moment();
    setToday(t.format('DD/MM/YYYY | h:mm'));
  }, [city]);

  useEffect(() => {
    let interval = setInterval(() => {
      setUpCountDownTimer();
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [timings]);

  const handleChange = async (event) => {
    const cityObject = avilableCities.find((city) => city.apiName === event.target.value);
    setCity(cityObject);
  };

  const getTimings = async () => {
    try {
      const response = await axios.get(`https://api.aladhan.com/v1/timingsByCity?country=${city.country}&city=${city.apiName}`);
      const newTimings = response.data.data.timings;
      setTimings(newTimings);
    } catch (error) {
      console.error('Error fetching timings:', error);
    }
  };

  const setUpCountDownTimer = () => {
    const momentNow = moment();
    let prayerIndex = 2;

    if (
      momentNow.isAfter(moment(timings['Fajr'], 'hh:mm')) &&
      momentNow.isBefore(moment(timings['Dhuhr'], 'hh:mm'))
    ) {
      prayerIndex = 1;
    } else if (
      momentNow.isAfter(moment(timings['Dhuhr'], 'hh:mm')) &&
      momentNow.isBefore(moment(timings['Asr'], 'hh:mm'))
    ) {
      prayerIndex = 2;
    } else if (
      momentNow.isAfter(moment(timings['Asr'], 'hh:mm')) &&
      momentNow.isBefore(moment(timings['Maghrib'], 'hh:mm'))
    ) {
      prayerIndex = 3;
    } else if (
      momentNow.isAfter(moment(timings['Maghrib'], 'hh:mm')) &&
      momentNow.isBefore(moment(timings['Isha'], 'hh:mm'))
    ) {
      prayerIndex = 4;
    } else if (
      momentNow.isAfter(moment(timings['Isha'], 'hh:mm')) &&
      momentNow.isBefore(moment(timings['Fajr'], 'hh:mm').add(1, 'day'))
    ) {
      prayerIndex = 0;
    }

    setNextPrayerIndex(prayerIndex);

    const nextPrayerObject = prayersArray[prayerIndex];
    const nextPrayerTime = timings[nextPrayerObject.key];

    const remainingTime = moment(nextPrayerTime, 'hh:mm').diff(momentNow);
    const durationRemainingTime = moment.duration(remainingTime);

    
    
  setRemainingTime(`${Math.abs(durationRemainingTime.hours())}:${Math.abs(durationRemainingTime.minutes())}:${Math.abs(durationRemainingTime.seconds())}`);
  //if (remainingTime < 0) {
  //  const midnightDiff = moment().endOf('day').diff(momentNow);
  //  console.log("midnight is", midnightDiff);
  //}
}

  return (
    <>
      <Grid id="navbar" container style={{ display: 'flex' }}>
        <Grid xs={6}>
          <div style={{ color: 'white', marginLeft: '400px' }}>
            <h1>{today}</h1>
            <h2>{city.displayName}</h2>
          </div>
        </Grid>
        <Grid xs={6}>
          <div style={{ color: 'white', marginRight: '200px' }}>
            <h1>متبقى حتى صلاة {prayersArray[nextPrayerIndex].displayName}</h1>
            <h2>{remainingTime}</h2>
          </div>
        </Grid>
      </Grid>
      <Divider style={{ borderColor: 'yellow' }} />
      <Stack id="prayers" direction="row" spacing={2}  justifyContent={"space-evenly"}  style={{marginTop:"50px",}}>
       <Prayer name="الفجر" time={timings.Fajr}   image={fajrImage }/>
       <Prayer name="الظهر" time={timings.Dhuhr} image={dhurImage}/>
       <Prayer name="العصر" time={timings.Asr} image={ asrImage }/>
       <Prayer name="المغرب" time={timings.Maghrib} image={maghribImage}/>
       <Prayer name="العشاء" time={timings.Isha} image={ishaaImage }/>
      </Stack>
      {/* finish cards*/}




      <Stack  id="menuselect" direction="row" spacing={2}  justifyContent={"space-evenly"}  style={{marginTop:"50px",}}>
      <FormControl style={{width:"20%", }}>
        <InputLabel id="demo-simple-select-label"  ><span  style={{color:"#bbb"}}>  المدينة </span></InputLabel>
        <Select  
          labelId="demo-simple-select-label"
          id="demo-simple-select"
         // value={age}
          label="المدينة"
          onChange={handleChange}
          style={{color:"white"}}
        >
          {avilableCities.map((city)=>{return( <MenuItem   value={city.apiName} key={city.apiName}>{city.displayName }</MenuItem>)})}
         
          
          {/*
          <MenuItem value={{displayName:"الرياض",apiName:"Riyadh"}}>الرياض</MenuItem>
          <MenuItem value={{displayName:"المدينة المنورة",apiName:"Al Madinah"}}> المدينة المنورة</MenuItem>

              */}
        </Select>
      </FormControl>
      </Stack>
  
  
  </>
   
  )
}
